nav.main {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.8);
  z-index: 1000; }
  nav.main li {
    list-style-type: none;
    opacity: 0;
    padding: 1rem;
    -webkit-animation: navitemin 1s 1 ease forwards;
    animation: navitemin 1s 1 ease forwards;
    -webkit-transform: scale(0.1) translate(0, 3rem) rotate(720deg);
    transform: scale(0.1) translate(0, 3rem) rotate(720deg); }
  nav.main li:nth-child(1) {
